"use server";

import fs from "fs";
import path from "path";
import { v4 as uuidv4 } from "uuid";
import { getAuthInfo } from "./admin-auth";
import { revalidatePath, unstable_noStore as noStore } from "next/cache";

const PAYMENTS_DB_PATH = path.join(process.cwd(), "data", "payments.json");

export interface Payment {
    id: string;
    bookingId: string;
    studioId: string;
    studioName: string;
    userName: string;
    userEmail: string;
    userPhone?: string;
    amount: number;
    status: "paid" | "pending" | "failed";
    paymentMethod: "stripe" | "credit" | "other" | "bank_transfer";
    date: string;
    dueDate?: string;
    createdAt: string;
}

// Ensure DB exists
if (!fs.existsSync(PAYMENTS_DB_PATH)) {
    fs.writeFileSync(PAYMENTS_DB_PATH, JSON.stringify([], null, 2));
}

// Get all payments (internal helper)
async function _getAllPayments(): Promise<Payment[]> {
    noStore(); // Bypass Next.js cache
    try {
        const data = fs.readFileSync(PAYMENTS_DB_PATH, "utf-8");
        return JSON.parse(data);
    } catch (error) {
        return [];
    }
}

// Get payments based on auth
export async function fetchPayments(): Promise<Payment[]> {
    const auth = await getAuthInfo();
    const payments = await _getAllPayments();

    if (auth.isAdmin) {
        return payments;
    }

    if (auth.studioId) {
        return payments.filter(p => p.studioId === auth.studioId);
    }

    return [];
}

// Send reminder email for pending payment
export async function sendReminderEmail(
    paymentId: string,
    email: string
): Promise<{ success: boolean }> {
    console.log(`Sending reminder email to ${email} for payment ${paymentId}`);
    await new Promise((resolve) => setTimeout(resolve, 800));
    return { success: true };
}

// Create a new payment record (called when booking is created)
export async function createPayment(data: {
    bookingId: string;
    studioId: string;
    studioName: string;
    userName: string;
    userEmail: string;
    userPhone?: string;
    amount: number;
    paymentMethod: "stripe" | "credit" | "other" | "bank_transfer";
}): Promise<{ success: boolean; paymentId: string }> {
    const payments = await _getAllPayments();

    const newPayment: Payment = {
        id: uuidv4(),
        ...data,
        status: "pending",
        date: new Date().toISOString().split("T")[0],
        dueDate: new Date(Date.now() + 3 * 24 * 60 * 60 * 1000)
            .toISOString()
            .split("T")[0],
        createdAt: new Date().toISOString(),
    };

    payments.push(newPayment);
    fs.writeFileSync(PAYMENTS_DB_PATH, JSON.stringify(payments, null, 2));

    return { success: true, paymentId: newPayment.id };
}

// Update payment status (manual reconciliation or webhook)
export async function updatePaymentStatus(
    paymentId: string,
    status: "paid" | "pending" | "failed"
): Promise<{ success: boolean; message?: string }> {
    try {
        const payments = await _getAllPayments();
        const paymentIndex = payments.findIndex((p) => p.id === paymentId);

        if (paymentIndex === -1) {
            return { success: false, message: "Payment not found" };
        }

        payments[paymentIndex].status = status;
        fs.writeFileSync(PAYMENTS_DB_PATH, JSON.stringify(payments, null, 2));

        // Revalidate to update server components
        revalidatePath("/admin/payments");
        revalidatePath("/admin");

        return { success: true };
    } catch (error: any) {
        console.error("Payment update error:", error);
        return { success: false, message: error.message };
    }
}

